body {
    background-color: #222222;
    text-align: center;
    font-family: 'Consolas';
}
h1 {
    color:seashell;
    font-family: 'Times New Roman', Times, serif;
    font-weight: bolder;
    font-size: 72px;
}
p, label {
    color: #bbbbbb;
}
h3 {
    font-weight:lighter;
    color: white;
}
#board {
    margin: auto;
    width: 50%;
}

#turnSpan {
    font-size: 30px;
    font-weight: bolder;
    color:royalblue;
}

#startButton {
    border-radius: 5%;
    border-width: 0px;
    background-color: teal;
    margin-top: 50px;
    height: 30px;
    font-weight: bold;
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif;
}

#startButton:hover {
    background-color: darkcyan;
}

#choices {
    margin: auto;
    width: 20%;
    height: 20%;
    border: 1px solid gray;
}

#1, #2, #3, #4, #5, #6, #7, #8 {
    border-radius: 20%;
    background-color: #bbbbbb;
}